html body {
  margin: 0;
  padding: 0;
  width: 100%;
  font-family: "Helvetica Neue", sans-serif;
}
a {
  text-decoration: none;
  color: black;
}
input {
    width: 100%;
    max-width: 100%;
    color: #313131;
    padding: 6px;
    line-height: 30px;
    display: block;
    font-weight: 100;
    font-size: 12px;
    position: relative;
    z-index: 2;
    -webkit-appearance: none;
    height: 100%;
    margin: 0 !important;
    border: 1px solid #d8d8d8;
    outline: 0;
    text-align: center;
}

input[type="text"]{
    font-weight: 100;
    font-size:24px;
}

img {
  width: 40%;
  height: 17em;
}

img:hover{
  position: absolute;
  width: 20%;
  height: 20em;
}

.navigation {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  justify-content: center;
  width: 100%;
  margin: 5px;
  letter-spacing: 2px;
  font-size: 25px;
}

.list {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-auto-rows: auto;
  margin-top: 1em;
  margin: 2em 2em;
}

.card-container{
  display: flex;
  align-content: center;
  margin-top: 3em;
}

.desc{
  margin-left: 1em;
  width: 50%;
}